import { useEffect, useState, useRef } from "react";
// import { HiArrowUp, HiArrowDown } from "react-icons/hi";
// Tshower.png
import nFoggy from "../img/Nimg/Foggy.png";
import nHail from "../img/Nimg/Hail.png";
import nLrain from "../img/Nimg/Lrain.png";
import nLsnow from "../img/Nimg/Lsnow.png";
import nmcloudy from "../img/Nimg/Mcloudy.png";
import nmoon from "../img/Nimg/Moon.png";
import npcloudy from "../img/Nimg/Pcloudy.png";
import nRain from "../img/Nimg/Rain.png";
import nSleet from "../img/Nimg/Sleet.png";
import nSnow from "../img/Nimg/Snow.png";
import nThunderstorm from "../img/Nimg/Tshower.png";
import nTstorm from "../img/Nimg/Tstorm.png";

import Foggy from "../img/Foggy.png";
import Hail from "../img/Hail.png";
import Lrain from "../img/Lrain.png";
import Lsnow from "../img/Lsnow.png";
import mcloudy from "../img/Mcloudy.png";
import pcloudy from "../img/Pcloudy.png";
import Rain from "../img/Rain.png";
import Sleet from "../img/Sleet.png";
import Snow from "../img/Snow.png";
import Thunderstorm from "../img/Tshower.png";
// import Tstorm from "../img/tstorm.png";
import sunny from "../img/Sunny.png";
import plmnn from "./Ntime.json";

// import Fla from "./Fla";
function Fla(item) {
  const [ntimee, setNtimee] = useState(false);
  const [nntimee, setNNtimee] = useState(false);

  const ntime = item.fahd[1] + item.fahd[2];
  useEffect(() => {
    const frt = plmnn.Ntime.forEach((element) => {
      if (element.toString() === ntime.toString()) setNNtimee(true);
    });
  }, []);
  const img = () => {
    if (nntimee) {
      if (item.comment === "Thunderstorm") {
        return nThunderstorm;
      } else if (item.comment === "Rain") {
        return nRain;
      } else if (item.comment === "Light rain showers") {
        return nLrain;
      } else if (item.comment === "Partly cloudy") {
        return npcloudy;
      } else if (item.comment === "Scattered thunderstorms") {
        return nLrain;
      } else if (item.comment === "Isolated thunderstorms") {
        return nLrain;
      } else if (item.comment === "Showers") {
        return nLrain;
      } else if (item.comment === "Sunny") {
        return nmoon;
      } else if (item.comment === "Clear") {
        return nmoon;
      } else if (item.comment === "Cloudy") {
        return nmcloudy;
      } else if (item.comment === "Mostly cloudy") {
        return nmcloudy;
      } else {
        return item.iconURL;
      }
    } else {
      if (item.comment === "Thunderstorm") {
        return Thunderstorm;
      } else if (item.comment === "Rain") {
        return Rain;
      } else if (item.comment === "Rain") {
        return Lrain;
      } else if (item.comment === "Light rain showers") {
        return Lrain;
      } else if (item.comment === "Partly cloudy") {
        return pcloudy;
      } else if (item.comment === "Scattered thunderstorms") {
        return Lrain;
      } else if (item.comment === "Isolated thunderstorms") {
        return Lrain;
      } else if (item.comment === "Showers") {
        return Lrain;
      } else if (item.comment === "Sunny") {
        return sunny;
      } else if (item.comment === "Clear") {
        return sunny;
      } else if (item.comment === "Cloudy") {
        return mcloudy;
      } else if (item.comment === "Mostly cloudy") {
        return mcloudy;
      } else {
        return item.iconURL;
      }
    }
  };
  // forEach
  // const frt = plmnn.Ntime.forEach((element) => {
  //   if ((element = ntime)) setNNtimee("trueeeeeeeeeeee");
  // });

  return (
    <div className="cars">
      {/* <img src={item.iconURL} alt={item.comment} /> */}
      <img src={img()} alt={item.comment} />
      <h1>{ntimee}</h1>
      <h1>{nntimee.toString()}</h1>
    </div>
  );
}

export default Fla;
