import React, { useEffect, useState } from "react";
import {
  TbCloudRain,
  TbNumber0,
  TbTemperatureCelsius,
  TbWind
} from "react-icons/tb";
import { WiHumidity } from "react-icons/wi";
import { MdOutlineLocationOn } from "react-icons/md";
import { BiLocationPlus } from "react-icons/bi";
import Imgg from "../src/components/Imgg";
import Fla from "../src/components/Fla";
import Search from "../src/components/Search";
import Ntime from "../src/components/Ntime";
import "./styles.css";
// import API from "./components/Api";
// import Con from "./components/Con";
import Con from "./components/Con";
import Thunderstorm from "./img/Tshower.png";
import pcloudy from "./img/Pcloudy.png";
import npcloudy from "./img/Nimg/Pcloudy.png";
import Lrain from "./img/Lrain.png";
import sunny from "./img/Sunny.png";
